import { supabase } from '@/lib/supabaseClient';
import styles from "./candidatos.module.css";

export default async function CargoPage({ params }: { params: { cargo: string } }) {
  const { cargo } = params;

  // ðŸ”¹ Obtener candidatos filtrados por cargo y unir datos de la tabla partidos
  const { data: candidatos, error } = await supabase
    .from("candidatos")
    .select(`
      id,
      nombre,
      cargo,
      entidad,
      partido_id (
        id,
        nombre,
        coalicion,
        pagina_web
      )
    `)
    .eq("cargo", cargo);

  if (error) {
    console.error("Error cargando candidatos:", error.message);
    return <p>Error cargando candidatos</p>;
  }

  if (!candidatos || candidatos.length === 0) {
    return <p>No hay candidatos para el cargo "{cargo}"</p>;
  }

  return (
    <div className={styles.container}>
      <h1 className={styles.header}>Candidatos para {cargo}</h1>
      <ul className={styles.lista}>
        {candidatos.map((candidato) => (
          <li key={candidato.id} className={styles.card}>
            <h2 className={styles.nombre}>{candidato.nombre}</h2>
            <p><strong>Cargo:</strong> {candidato.cargo}</p>
            <p><strong>Entidad:</strong> {candidato.entidad}</p>
            <p>
              <strong>Partido:</strong>{" "}
              {candidato.partido_id?.nombre || "Independiente"}
            </p>
            {candidato.partido_id?.coalicion && (
              <p><strong>CoaliciÃ³n:</strong> {candidato.partido_id.coalicion}</p>
            )}
            {candidato.partido_id?.pagina_web && (
              <p>
                <a
                  href={candidato.partido_id.pagina_web}
                  target="_blank"
                  rel="noopener noreferrer"
                >
                  PÃ¡gina del partido
                </a>
              </p>
            )}
          </li>
        ))}
      </ul>
    </div>
  );
}
